'use client'

import { useEffect, useState } from 'react'
import { useSearchParams } from 'next/navigation'
import Navigation from '@/components/Navigation'
import { supabase } from '@/lib/supabase'
import ConversationList from '@/components/ConversationList'
import MessageThread from '@/components/MessageThread'
import { MessageSquare, Send, X } from 'lucide-react'

export default function MessengerPage() {
  const searchParams = useSearchParams()
  const [conversations, setConversations] = useState([])
  const [selectedConversation, setSelectedConversation] = useState(null)
  const [loading, setLoading] = useState(true)
  
  // New message compose state
  const [composeMode, setComposeMode] = useState(false)
  const [composePhone, setComposePhone] = useState('')
  const [composeName, setComposeName] = useState('')
  const [composeMemberId, setComposeMemberId] = useState('')
  const [composeMessage, setComposeMessage] = useState('')
  const [sending, setSending] = useState(false)

  useEffect(() => {
    loadConversations()
    
    // Check if we're starting a new message from URL params
    const phone = searchParams.get('phone')
    const name = searchParams.get('name')
    const memberId = searchParams.get('memberId')
    
    if (phone && name) {
      setComposeMode(true)
      setComposePhone(phone)
      setComposeName(name)
      setComposeMemberId(memberId || '')
    }
    
    // Subscribe to real-time updates
    const channel = supabase
      .channel('messages')
      .on('postgres_changes', 
        { event: '*', schema: 'public', table: 'messages' },
        () => {
          loadConversations()
          if (selectedConversation) {
            loadMessages(selectedConversation.id)
          }
        }
      )
      .subscribe()

    return () => {
      supabase.removeChannel(channel)
    }
  }, [searchParams])

  async function loadConversations() {
    try {
      const { data, error } = await supabase
        .from('conversations')
        .select(`
          *,
          member:members(id, name, phone, phone_e164),
          messages(id, created_at, body, direction)
        `)
        .order('last_message_at', { ascending: false, nullsFirst: false })

      if (error) throw error

      setConversations(data || [])
    } catch (error) {
      console.error('Error loading conversations:', error)
    } finally {
      setLoading(false)
    }
  }

  async function loadMessages(conversationId) {
    try {
      const { data, error } = await supabase
        .from('conversations')
        .select(`
          *,
          member:members(id, name, phone, phone_e164),
          messages(*)
        `)
        .eq('id', conversationId)
        .single()

      if (error) throw error

      setSelectedConversation(data)
      setComposeMode(false) // Exit compose mode when viewing a conversation
    } catch (error) {
      console.error('Error loading messages:', error)
    }
  }

  async function sendNewMessage() {
    if (!composeMessage.trim() || !composePhone) return

    setSending(true)
    try {
      // Call your API route to send via BlueBubbles
      const response = await fetch('/api/send-message', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          phone: composePhone,
          message: composeMessage,
          memberId: composeMemberId
        }),
      })

      if (!response.ok) {
        const error = await response.json()
        throw new Error(error.error || 'Failed to send message')
      }

      // Success! Clear form and refresh conversations
      setComposeMessage('')
      alert(`Message sent to ${composeName}!`)
      
      // Reload conversations to see the new one
      await loadConversations()
      
      // Exit compose mode
      setComposeMode(false)
    } catch (error) {
      console.error('Error sending message:', error)
      alert(`Failed to send message: ${error.message}`)
    } finally {
      setSending(false)
    }
  }

  function startNewCompose() {
    setComposeMode(true)
    setComposePhone('')
    setComposeName('')
    setComposeMemberId('')
    setComposeMessage('')
    setSelectedConversation(null)
  }

  if (loading) {
    return (
      <div>
        <Navigation />
        <div className="flex items-center justify-center h-screen">
          <div className="text-gray-500">Loading conversations...</div>
        </div>
      </div>
    )
  }

  return (
    <div>
      <Navigation />
      <div className="h-screen flex flex-col">
        <div className="flex-1 flex overflow-hidden">
          {/* Conversations Sidebar */}
          <div className="w-80 bg-white border-r border-gray-200 flex flex-col">
            <div className="p-4 border-b border-gray-200 flex items-center justify-between">
              <h2 className="text-lg font-semibold text-gray-900">
                Conversations
              </h2>
              <button
                onClick={startNewCompose}
                className="inline-flex items-center px-3 py-1.5 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700"
                title="New Message"
              >
                <MessageSquare className="h-4 w-4 mr-1" />
                New
              </button>
            </div>
            <ConversationList
              conversations={conversations}
              selectedId={selectedConversation?.id}
              onSelect={(conv) => loadMessages(conv.id)}
            />
          </div>

          {/* Main Content Area */}
          <div className="flex-1 flex flex-col bg-gray-50">
            {composeMode ? (
              /* New Message Compose View */
              <div className="flex-1 flex flex-col">
                <div className="bg-white border-b border-gray-200 px-6 py-4 flex items-center justify-between">
                  <h2 className="text-lg font-semibold text-gray-900">
                    New Message {composeName && `to ${composeName}`}
                  </h2>
                  <button
                    onClick={() => setComposeMode(false)}
                    className="text-gray-400 hover:text-gray-500"
                  >
                    <X className="h-5 w-5" />
                  </button>
                </div>

                <div className="flex-1 p-6 overflow-y-auto">
                  <div className="max-w-2xl mx-auto space-y-4">
                    {/* Recipient Info */}
                    {composeName ? (
                      <div className="bg-blue-50 rounded-lg p-4 border border-blue-200">
                        <p className="text-sm font-medium text-blue-900">Sending to:</p>
                        <p className="text-lg font-semibold text-blue-900">{composeName}</p>
                        <p className="text-sm text-blue-700">{composePhone}</p>
                      </div>
                    ) : (
                      <div className="space-y-2">
                        <label htmlFor="phone" className="block text-sm font-medium text-gray-700">
                          Recipient Phone Number (E.164 format)
                        </label>
                        <input
                          id="phone"
                          type="text"
                          placeholder="+15735551234"
                          value={composePhone}
                          onChange={(e) => setComposePhone(e.target.value)}
                          className="block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                        />
                      </div>
                    )}

                    {/* Message Input */}
                    <div className="space-y-2">
                      <label htmlFor="message" className="block text-sm font-medium text-gray-700">
                        Message
                      </label>
                      <textarea
                        id="message"
                        rows={6}
                        placeholder="Type your message..."
                        value={composeMessage}
                        onChange={(e) => setComposeMessage(e.target.value)}
                        className="block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                      />
                    </div>

                    {/* Send Button */}
                    <button
                      onClick={sendNewMessage}
                      disabled={sending || !composeMessage.trim() || !composePhone}
                      className="w-full inline-flex justify-center items-center px-4 py-3 border border-transparent text-base font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:bg-gray-400 disabled:cursor-not-allowed"
                    >
                      {sending ? (
                        <>
                          <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                          Sending...
                        </>
                      ) : (
                        <>
                          <Send className="h-5 w-5 mr-2" />
                          Send Message
                        </>
                      )}
                    </button>
                  </div>
                </div>
              </div>
            ) : selectedConversation ? (
              /* Existing Conversation Thread */
              <MessageThread
                conversation={selectedConversation}
                onMessageSent={() => {
                  loadConversations()
                  loadMessages(selectedConversation.id)
                }}
              />
            ) : (
              /* Empty State */
              <div className="flex-1 flex items-center justify-center text-gray-400">
                <div className="text-center">
                  <MessageSquare className="mx-auto h-12 w-12 mb-4" />
                  <p className="text-lg">Select a conversation or start a new message</p>
                  <button
                    onClick={startNewCompose}
                    className="mt-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700"
                  >
                    <MessageSquare className="mr-2 h-4 w-4" />
                    New Message
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}